body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: yellow;
  margin: 25px;
  align-content: center!important;
  border: 10px dotted red;
  
}

form {
  text-align: center;
  padding-top: 0;
  margin-left: 40%;
  margin-right: 40%;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  
}

.App {
  align-items: center;
}

.center {
  color: red;
}

p {
  color: red;

}

.buttonup {
  background-color: green;
  text-align: center;
  width: 120px;
  position: relative;
  color: black;
  font-weight: bold;
  
}

.buttondown {
  background-color: purple;
  text-align: center;
  width: 120px;
  position: relative;
  color: black;
  font-weight: bold;
  margin-bottom: 3rem;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.both {
    display: block;
    margin-left: auto;
    margin-right: auto;
  
}

p{
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  font-weight: 4px;
  font-size: 16pt;

}

h4 {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.last {
 border: 3px solid black;
 width: 200px;
 font-weight: bold;
}

.last:hover {
  opacity: 50%;
  transform: translateY(-0.5%);
  box-shadow: 0 4rem 8rem black;
}